import React from 'react';
import { ErrorMessage } from 'formik';
import Cleave from 'cleave.js/react';
import classes from './Price.module.scss';

import P from '../../re-use/p/P';

function Price(props) {
	const [ price, setPrice ] = React.useState('');

	const handleErr = () => {
		if (props.props.touched.price && props.props.errors.price) {
			return classes.inputinvalid;
		} else {
			return '';
		}
	};
	const cls = handleErr();

	const handleCleave = (e, props) => {
		const rawValueLength = e.target.rawValue.length;
		const rawValue = e.target.rawValue;
		const value = e.target.value;
		if (rawValueLength < 4) {
			setPrice(`تومان ${value}`);
		} else if (rawValueLength < 7) {
			setPrice(`هزار تومان ${value}`);
		} else if (rawValueLength < 10) {
			setPrice(`میلیون تومان ${value}`);
		} else {
			setPrice(`میلیارد تومان ${value}`);
		}
		props.setFieldValue('price', parseInt(rawValue));
	};

	return (
		<React.Fragment>
			<P>قیمت</P>
			<Cleave
				className={`${classes.price} ${cls}`}
				onChange={(e) => handleCleave(e, props.props)}
				options={{
					numeral: true,
					numeralPositiveOnly: true,
					numericOnly: true,
					numeralIntegerScale: 12,
					numeralDecimalScale: 0
				}}
				onBlur={() => props.props.setFieldTouched('price')}
			/>

			<P class="mt-1">{price}</P>

			<P class="mt-2 text-danger">
				<ErrorMessage name="price" />
			</P>
		</React.Fragment>
	);
}

export default Price;
