import React from 'react';
import { Input } from 'antd';
import { ErrorMessage } from 'formik';

import P from '../../re-use/p/P';
import classes from '../add-title/AddTitle.module.scss';

const { TextArea } = Input;

function AddComment(props) {
	const handleErr = () => {
		if (props.props.touched.desc && props.props.errors.desc) {
			return classes.inputinvalid;
		} else {
			return '';
		}
	};
	const cls = handleErr();
	return (
		<React.Fragment>
			<P>توضیحات آگهی</P>
			<TextArea
				onBlur={() => props.props.setFieldTouched('desc')}
				className={`${classes.input} ${cls}`}
				placeholder="توضیحات ..."
				onChange={(e) => props.props.setFieldValue('desc', e.target.value)}
				autoSize={{ maxRows: 4, minRows: 4 }}
			/>
			<P class="mt-2 text-danger">
				<ErrorMessage name="desc" />
			</P>
		</React.Fragment>
	);
}

export default AddComment;
