import React from 'react';
import Axios from 'axios';
import { withRouter } from 'react-router-dom';
import { Button, Modal } from 'antd';
import classes from '../AddPage.module.scss';
import P from '../../re-use/p/P';

function DeleteEditAdd(props) {
	const [ visible, setVisible ] = React.useState(false);
	const [ loading, setLoading ] = React.useState(false);

	const showModal = () => {
		setVisible(true);
	};

	const handleOk = () => {
		setLoading(true);
		const id = props.id;
		Axios.post(
			'https://ravid-app.herokuapp.com/deleteAd',
			{ id: id },
			{ headers: { Authorization: `bearer ${localStorage.getItem('token')}` } }
		)
			.then((res) => {
				console.log(res.data);
			})
			.then(() => {
				setLoading(false);
				props.history.push('/');
			})
			.catch((e) => {
				console.log(e.message + 'Error bro');
			});
	};

	const handleCancel = (e) => {
		setVisible(false);
	};

	return (
		<div className="container">
			<div className={classes.btn}>
				<Modal
					title="حذف آگهی"
					visible={visible}
					onOk={handleOk}
					onCancel={handleCancel}
					footer={[
						<Button size="large" onClick={handleCancel}>
							بازگشت
						</Button>,
						<Button size="large" danger type="primary" loading={loading} onClick={handleOk}>
							حذف آگهی
						</Button>
					]}
				>
					<P>از حذف آگهی مطمئن هستید ؟</P>
				</Modal>

				<Button style={{ marginBottom: '1em' }} type="primary" size="large" danger onClick={showModal}>
					حذف آگهی
				</Button>

				<Button
					style={{ marginBottom: '1em' }}
					onClick={() => {
						props.setShowHandle();
						window.scrollTo(0, 0);
					}}
					className="mr-2"
					type="primary"
					size="large"
				>
					ویرایش آگهی
				</Button>
			</div>
		</div>
	);
}

export default withRouter(DeleteEditAdd);
