import React from 'react';
import { ArrowLeftOutlined } from '@ant-design/icons';
import * as yup from 'yup';
import { Formik, Form } from 'formik';
import { withRouter } from 'react-router-dom';
import Axios from 'axios';

// Components
import BootstrapContainer from '../../re-use/bootstrap-container/BootstrapContainer';
import AddItemContainer from '../../re-use/add/AddItemContainer';
import Price from '../../add/price/Price';
import AddContainer from '../../re-use/add/AddContainer';
import AddComment from '../../add/add-comment/AddComment';
import AddTitle from '../../add/add-title/AddTitle';
import SubmitBtn from '../../add/submit-btn/SubmitBtn';

const initialValues = {
	title: '',
	desc: '',
	price: ''
};

const validationSchema = yup.object({
	title: yup
		.string()
		.typeError('عنوان انتخاب نشده')
		.max(14, 'عنوان باید کمتر از 14 حروف باشد')
		.required('عنوان آگهی انتخاب نشده'),
	desc: yup
		.string()
		.typeError('توضیحات انتخاب نشده')
		.max(100, 'توضیحات باید کمتر از 50 حروف باشد')
		.required('توضیحات آگهی انتخاب نشده'),

	price: yup
		.number('قیمت انتخاب نشده')
		.typeError('قیمت انتخاب نشده')
		.integer('قیمت انتخاب نشده')
		.positive()
		.required('قیمت آگهی انتخاب نشده')
});

function EditAdComp(props) {
	const [ loading, setLoading ] = React.useState(false);

	const submit = (e) => {
		const id = props.match.params.id;

		setLoading(true);
		Axios.post(
			'https://ravid-app.herokuapp.com/editAd',
			{
				title: e.title,
				desc: e.desc,
				price: e.price,
				id: id
			},
			{ headers: { Authorization: `bearer ${localStorage.getItem('token')}` } }
		)
			.then(() => {
				setLoading(false);
				props.setShowHandle();
				props.history.push('/account');
			})
			.catch((e) => {
				console.log(e.message + 'Axios err');
			});
	};

	return (
		<React.Fragment>
			<ArrowLeftOutlined
				onClick={() => props.setShowHandle()}
				style={{
					color: '#B3B3B3',
					fontSize: 42,
					marginLeft: '1em',
					marginTop: '1em',
					cursor: 'pointer'
				}}
			/>
			<Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={submit}>
				{(props) => {
					return (
						<Form>
							<BootstrapContainer>
								<AddContainer>
									<AddItemContainer class={`align-items-center `}>
										<AddTitle props={props} />
									</AddItemContainer>
								</AddContainer>
								<AddContainer>
									<AddItemContainer class={`align-items-center`}>
										<AddComment props={props} />
									</AddItemContainer>
								</AddContainer>
								<AddContainer>
									<AddItemContainer class={`align-items-end`}>
										<Price props={props} />
									</AddItemContainer>
								</AddContainer>
							</BootstrapContainer>
							<SubmitBtn marginBottom={'1em'} loading={loading} props={props} text={`ثبت ویرایش`} />
						</Form>
					);
				}}
			</Formik>
		</React.Fragment>
	);
}

export default withRouter(EditAdComp);
