import React from 'react';
import options from '../../app-data/options';
import { ArrowLeftOutlined } from '@ant-design/icons';
import { Spin } from 'antd';

// Components
import BootstrapContainer from '../re-use/bootstrap-container/BootstrapContainer';
import MediaDivider from '../re-use/media-divider/MediaDivider';
import Desc from './desc/Desc';
import AdImg from './ad-img/AdImg';
import AddAllDetails from './add-details/AddAllDetails';
import DeleteEditAdd from './delete-edit-add/DeleteEditAdd';
import EditAdComp from './edit-ad-component/EditAdComp';
import Axios from 'axios';
import { useHistory } from 'react-router-dom';
import * as moment from 'jalali-moment';

function AddPage(props) {
	const [ showEdit, setShowEdit ] = React.useState(false);
	const [ editDelete, setEditDelete ] = React.useState(false);
	const [ ad, setAd ] = React.useState('');
	const [ adLoc, setAdLoc ] = React.useState('');
	const [ spin, setSpin ] = React.useState(false);
	const go = useHistory();
	const setShowHandle = () => {
		setShowEdit(false);
	};
	const handleShow = () => {
		setShowEdit(true);
	};

	React.useEffect(
		() => {
			const id = props.match.params.id;
			setSpin(true);
			Axios.post('https://ravid-app.herokuapp.com/getAd', { id: id })
				.then((res) => {
					setAd(res.data);
					const adLoc = options.find((any) => any.url === res.data.location);
					setAdLoc(adLoc.value);
					setSpin(false);
				})
				.catch((e) => {
					console.log(e.message + 'Error bro');
				});
			if (localStorage.getItem('id') !== null) {
				Axios.post(
					'https://ravid-app.herokuapp.com/checkUserAd',
					{ user_id: localStorage.getItem('id'), ad_id: id },
					{ headers: { Authorization: `bearer ${localStorage.getItem('token')}` } }
				)
					.then((res) => {
						if (res.data === 'success') {
							setEditDelete(true);
						} else {
							setEditDelete(false);
						}
					})
					.catch((err) => {
						setEditDelete(false);
						console.log(err.message + 'Error borrr');
					});
			} else {
				setEditDelete(false);
			}
		},
		[ props.match ]
	);
	const e2p = (s) => s.replace(/\d/g, (d) => '۰۱۲۳۴۵۶۷۸۹'[d]);
	return (
		<React.Fragment>
			<Spin size={'large'} spinning={spin}>
				{showEdit ? (
					<EditAdComp setShowHandle={setShowHandle} />
				) : (
					<React.Fragment>
						<ArrowLeftOutlined
							onClick={() => {
								go.goBack();
							}}
							style={{
								color: '#B3B3B3',
								fontSize: 42,
								marginLeft: '.5em',
								marginTop: '.6em',
								cursor: 'pointer'
							}}
						/>

						<BootstrapContainer class="mt-4">
							<AdImg img={ad.img} />
							<AddAllDetails
								date={e2p(`${moment(ad.date).locale('fa').format('YY/M/D').toLocaleString('ar-EG')}`)}
								number={`${e2p(`0${ad.number || 9120000000}`)}`}
								price={
									(ad.price && ad.price.toLocaleString('ar-EG')) || (10000).toLocaleString('ar-EG')
								}
								locationTitle={`مکان`}
								location={adLoc || 'تهران'}
								title={ad.title || 'عنوان'}
							/>
						</BootstrapContainer>
						<MediaDivider />
						<BootstrapContainer>
							<Desc title={`توضیحات`} desc={ad.desc} />
						</BootstrapContainer>
						{editDelete ? <DeleteEditAdd id={props.match.params.id} setShowHandle={handleShow} /> : null}
					</React.Fragment>
				)}
			</Spin>
		</React.Fragment>
	);
}

export default AddPage;
