import React from 'react';

function AdImg(props) {
	const [ loaded, setLoaded ] = React.useState(false);

	const handleClass = () => {
		if (loaded) {
			return '';
		}
		return 'd-none';
	};
	const cls = handleClass();

	const handleErr = (e) => {
		e.target.src = require('../../../app-data/img/default.png');
	};

	return (
		<div className="col-lg-6 col-md-6 col-sm-12 col-12">
			<div className={`d-flex justify-content-center flex-column align-items-center `}>
				{!loaded ? (
					<img
						alt="ad-img"
						className="img-fluid rounded"
						src={require('../../../app-data/img/default.png')}
					/>
				) : null}

				<img
					alt="ad-img"
					className={`img-fluid rounded ${cls}`}
					src={props.img && `https://ravid-app.herokuapp.com/${props.img}`}
					onLoad={() => setLoaded(true)}
					onError={handleErr}
				/>
			</div>
		</div>
	);
}

export default AdImg;
