import React from 'react';
import P from '../../re-use/p/P';

function Desc(props) {
	return (
		<React.Fragment>
			<div className="col-12 mt-5">
				<P>
					<h2>{props.title}</h2>
				</P>
			</div>
			<div className="col-12 mt-2">
				<P>{props.desc}</P>
			</div>
		</React.Fragment>
	);
}

export default Desc;
