import React from 'react';
import { Divider } from 'antd';
import Media from 'react-media';

function MediaDivider() {
	return (
		<Media
			query="(min-width: 1024px)"
			render={() => {
				return <Divider className="m-0 p-0 mt-3" />;
			}}
		/>
	);
}

export default MediaDivider;
