import React from 'react';
import { Divider } from 'antd';
import classes from './AddAllDetails.module.scss';
import P from '../../re-use/p/P';

function AddAllDetails(props) {
	return (
		<div className={`col-lg-6 col-md-6 col-sm-12 col-12 ${classes.titleMargin}`}>
			<P>
				<h2>{props.title}</h2>
			</P>
			<Divider className="p-0 m-0" />
			<div className="d-flex justify-content-between">
				<P padding={10} margin={0} textAlign="left">
					{props.location}
				</P>
				<P padding={10} margin={0}>
					{props.locationTitle}
				</P>
			</div>
			<Divider className="p-0 m-0" />
			<div className="d-flex justify-content-between">
				<P padding={10} margin={0} textAlign="left">
					{`${`تومان`} ${props.price}`}
				</P>
				<P class={classes.price} padding={10} margin={0}>
					قیمت
				</P>
			</div>
			<Divider className="p-0 m-0" />
			<div className="d-flex justify-content-between">
				<P padding={10} margin={0} textAlign="left">
					{props.number}
				</P>
				<P padding={10} margin={0}>
					شماره تماس
				</P>
			</div>
			<Divider className="p-0 m-0" />
			<div className="d-flex justify-content-between">
				<P padding={10} margin={0} textAlign="left">
					{props.date}
				</P>
				<P padding={10} margin={0}>
					تاریخ انتشار
				</P>
			</div>
			<Divider className="p-0 m-0" />
		</div>
	);
}

export default AddAllDetails;
