import React from 'react';
import { Input } from 'antd';
import P from '../../re-use/p/P';
import { ErrorMessage } from 'formik';
import classes from './AddTitle.module.scss';

function AddTitle(props) {
	const handleErr = () => {
		if (props.props.touched.title && props.props.errors.title) {
			return classes.inputinvalid;
		} else {
			return '';
		}
	};
	const cls = handleErr();

	return (
		<React.Fragment>
			<P>عنوان آگهی</P>
			<Input
				onBlur={() => props.props.setFieldTouched('title')}
				className={`${classes.input} ${cls}`}
				placeholder="نام کالا..."
				onChange={(e) => {
					props.props.setFieldValue('title', e.target.value);
				}}
			/>
			<P class="mt-2 text-danger">
				<ErrorMessage name="title" />
			</P>
		</React.Fragment>
	);
}

export default AddTitle;
